const screenSize = 1024 - 768
export default {
  async asyncData({ $axios, params, redirect, error }) {
    let { data } = await $axios.get(`/api/${params.user}/`)
    if (!data) {
      redirect('/')
    }
    return {
      user: data,
    }
  },
  methods: {
    tabChange(key) {
      this.$router.push({ name: key })
    },
  },
}
